@charset "UTF-8";
@import url(../../node_modules/swiper/swiper-bundle.css);
body {
  color: #808080;
  background: #ffffff;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 26px; }

:focus {
  outline: none !important; }

h1, h2, h3, h4, h5, h6 {
  color: #333; }

/* default font size */
.fa {
  font-size: 14px; }

.fa-stack {
  width: 12px;
  font-size: 8px; }

.fa-stack .fa {
  font-size: 12px; }

/* Override the bootstrap defaults */
h1 {
  font-size: 33px; }

h2 {
  font-size: 27px; }

h3 {
  font-size: 21px; }

h4 {
  font-size: 15px; }

h5 {
  font-size: 12px; }

h6 {
  font-size: 10.2px; }

a {
  color: #262626; }

a:hover {
  text-decoration: none; }

legend {
  font-size: 18px;
  padding: 7px 0px; }

label {
  font-size: 14px;
  font-weight: 400;
  color: #262626; }

h1, h2, h3, h4, h5, h6 {
  color: #262626; }

select.form-control, textarea.form-control, input[type="text"].form-control, input[type="password"].form-control, input[type="datetime"].form-control, input[type="datetime-local"].form-control, input[type="date"].form-control, input[type="month"].form-control, input[type="time"].form-control, input[type="week"].form-control, input[type="number"].form-control, input[type="email"].form-control, input[type="url"].form-control, input[type="search"].form-control, input[type="tel"].form-control, input[type="color"].form-control {
  font-size: 14px; }

.input-group input, .input-group select, .input-group .dropdown-menu, .input-group .popover {
  font-size: 13px; }

.input-group .input-group-addon {
  font-size: 13px;
  height: 30px; }

button.addcart {
  margin-top: 5px;
  background: #FCB026;
  padding: 10px 26px;
  width: 90%;
  color: #000000;
  height: auto;
  font-size: 13px;
  font-weight: 500;
  line-height: 22px;
  letter-spacing: 0.6px;
  border: 1px solid #FCB026;
  border-radius: 30px;
  -webkit-border-radius: 30px;
  -khtml-border-radius: 30px;
  -moz-border-radius: 30px; }

button.addcart:hover {
  background: #000;
  border-color: #000;
  color: #fff; }

/* Fix some bootstrap issues */
span.hidden-xs, span.hidden-sm, span.hidden-md, span.hidden-lg {
  display: inline-block !important; }

.nav-tabs {
  margin-bottom: 15px; }

div.required .control-label:before {
  content: '* ';
  color: #F00;
  font-weight: bold; }

/* Gradent to all drop down menus */
.dropdown-menu li > a:hover {
  text-decoration: none;
  color: #888; }

/* top */
#top {
  background: black;
  line-height: initial;
  color: #ffffff;
  font-size: 12px;
  font-weight: 400;
  text-align: left;
  letter-spacing: 0.7px; }

#top a {
  color: #fff; }

#top a:hover {
  color: #fcb026; }

.social-block {
  display: inline-block;
  margin-top: 6px;
  float: right; }

.social-block ul {
  padding: 0px;
  margin: 0; }

.social-block ul li {
  display: inline-block;
  text-align: center;
  margin: 0;
  margin-right: 10px; }

.social-block ul li a {
  color: #555;
  display: inline-block; }

.social-block ul li a span {
  display: none;
  background-color: #000; }

.social-block ul li a:before {
  font-size: 18px;
  font-family: "FontAwesome";
  display: inline-block;
  font-weight: normal;
  height: 35px;
  width: 35px;
  color: #444444;
  line-height: 30px;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.social-block ul li:hover a:before {
  color: black; }

.social-block ul li a:hover:before {
  color: black; }

.social-block ul li.facebook a:before {
  content: "\F09A"; }

.social-block ul li.twitter a:before {
  content: "\F099"; }

.social-block ul li.rss a:before {
  content: "\F09E"; }

.social-block ul li.youtube a:before {
  content: "\F16A"; }

.social-block ul li.googleplus a:before {
  content: "\F0D5"; }

.social-block ul li.instagram a:before {
  content: "\F16D"; }

#top-links span {
  text-transform: capitalize; }

#form-currency .currency-select {
  text-align: left;
  width: 100%; }

#form-currency .currency-select:hover {
  text-shadow: none;
  color: #262626;
  background-color: #fff; }

#form-language .dropdown-menu,
#form-currency .dropdown-menu {
  padding: 0px;
  top: 44px;
  left: auto;
  right: 0;
  margin: 0;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1); }

#form-language .dropdown-menu .btn,
#form-currency .dropdown-menu .btn {
  font-size: 13px;
  padding: 8px 15px;
  display: block;
  color: #000;
  float: left;
  width: 100%;
  margin: 0;
  text-align: left; }

#form-language .dropdown-menu > li > .btn:hover,
#form-currency .dropdown-menu .btn:hover {
  color: black;
  background: transparent; }

header .btn-link, header li, header a {
  text-decoration: none; }

header li {
  list-style: none; }

/* header cms start*/
.headertopright {
  float: right;
  position: relative;
  z-index: 999; }

#form-language img {
  vertical-align: inherit;
  padding-right: 3px; }

#top-links li, .header-search li {
  padding: 0;
  list-style: none; }

header .btn-link:hover, header .btn-link:focus, #top-links a:hover, #top-links a:focus {
  color: #ffd101;
  background: none;
  text-decoration: none; }

#top-links .dropdown-menu a {
  text-shadow: none; }

#top-links .dropdown-menu a:hover, #top-links .dropdown-menu a:focus {
  color: black; }

#top .btn-link strong {
  font-weight: normal; }

#top-links {
  padding-top: 0px;
  float: right; }

#top-links a + a {
  margin-left: 15px; }

#top-links ul.list-inline {
  float: left;
  margin-top: 2px;
  margin-right: 5px; }

#top-links ul.list-inline li {
  padding: 0 15px 0 8px;
  font-weight: 300; }

