.lang-curr {
  float: right;
  padding: 0px; }

.lang-curr .pull-left {
  padding-right: 0;
  width: auto;
  text-align: left;
  float: none;
  display: inline-block; }

.lang-curr .pull-left ul {
  padding: 0px; }

.lang-curr .pull-left li {
  display: block; }

#form-currency .currency-menu li .btn-link,
#form-language .language-menu li .btn-link {
  padding: 10px 15px;
  font-weight: normal;
  color: #666666;
  text-transform: capitalize;
  line-height: initial;
  border-bottom: 1px solid #ededed; }

#form-currency .currency-menu li:last-child .btn-link,
#form-language .language-menu li:last-child .btn-link {
  border: none; }

#form-currency .currency-menu li .btn-link:hover,
#form-language .language-menu li .btn-link:hover {
  color: black; }

.lang-curr .fa {
  padding-left: 3px; }

#form-language, #form-currency {
  padding: 0px;
  margin-right: 0px; }

.fa-caret-down {
  vertical-align: middle;
  color: #b2b2b2;
  margin: 0 0 0 2px; }

#form-currency .btn-link, #form-language .btn-link {
  padding: 10px 0px;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 12px;
  line-height: inherit;
  text-decoration: none;
  color: #fff;
  letter-spacing: .6px; }

#form-currency .btn-link:hover, #form-language .btn-link:hover {
  color: #FCB026; }

#form-language .btn-link {
  margin-right: 20px; }

.headertopright .text2 a:hover {
  color: black; }

#form-currency .btn-link strong {
  font-weight: normal;
  color: #ffffff;
  -webkit-transition: all 0.5s ease 0s;
  -moz-transition: all 0.5s ease 0s;
  -o-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s; }

#form-currency .btn-link:hover strong {
  color: #FCB026;
  -webkit-transition: all 0.5s ease 0s;
  -moz-transition: all 0.5s ease 0s;
  -o-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s; }

.dropdown.myaccount a {
  color: #ffffff; }

.dropdown.myaccount .fa {
  padding-left: 7px;
  font-size: 15px; }

.dropdown.myaccount {
  display: inline-block;
  float: none;
  padding: 13px 24px 13px 0;
  width: auto; }

.dropdown.myaccount:hover a {
  color: #FCB026; }

.dropdown.myaccount .myaccount-menu {
  margin-top: 0px;
  top: 44px;
  min-width: 180px;
  padding: 0px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1); }

.dropdown.myaccount .myaccount-menu li a {
  font-size: 13px;
  padding: 10px 15px;
  display: block;
  font-weight: normal;
  color: #666666;
  float: left;
  width: 100%;
  margin: 0;
  text-align: left;
  text-transform: capitalize;
  line-height: initial;
  border-bottom: 1px solid #ededed; }

.dropdown.myaccount .myaccount-menu li:last-child a {
  border: none; }

.dropdown.myaccount .myaccount-menu li a:hover {
  color: #ffff;
  background-color: black; }

/* logo */
.header-logo {
  padding: 15px 0 0;
  text-align: center;
  display: inline-block;
  float: left; }

.header-search {
  float: left; }

.header-cart {
  text-align: center;
  position: relative;
  float: right;
  width: auto;
  z-index: 9;
  padding: 10px 0px;
